export default function (state = [], action) {
  const timestamp = new Date();

  switch (action.type) {
    case 'INCREASE_SCORE':
      return [...state, { player: action.id, points: action.amount, timestamp, extra: '' }];
    case 'DECREASE_SCORE':
      return [...state, { player: action.id, points: -action.amount, timestamp, extra: '' }];
    case 'MARK_CHICAGO':
      return [...state, { player: action.id, points: 15, timestamp: new Date(), extra: 'fick chicago!' }];
    case 'RESET_SCORES':
      return [];
    default:
      return state;
  }
}



