import React from 'react';
import Moment from 'react-moment';
import PropTypes from 'prop-types';

class Log extends React.Component {

  static format(type, value) {
    const formatting = {

      player: () => <span>{ value }</span>,

      timestamp: () => <Moment locale="sv" interval={15000} fromNow>{ value }</Moment>,

      extra: () => <span style={{ color: 'gray' }}>{ value }</span>,

      points: () => {
        let color = 'green';
        let symbol = '+';

        if (value < 1) {
          color = 'red';
          symbol = '-';
        }

        return <span style={{ color }}>{ `${symbol} ${Math.abs(value)}` }</span>;
      },

    };

    return formatting[type]();
  }

  componentDidUpdate() {
    this.bottom.scrollIntoView();
  }

  nameOf(pid) {
    const player = this.props.Players.find(p => p.id === pid);
    return player ? player.name : '(borttagen)';
  }

  render() {
    const logEntries = this.props.Log.map(
      (e, index) =>
      (
        <tr key={index} className="content is-small">
          <td className="animated fadeIn"> { Log.format('points', e.points) } </td>
          <td className="animated fadeIn"> { Log.format('player', this.nameOf(e.player)) } </td>
          <td className="animated fadeIn"> { Log.format('extra', e.extra) } </td>
          <td className="animated fadeIn"> { Log.format('timestamp', e.timestamp) } </td>
        </tr>
      ),
    );

    return (
      <div style={{ overflow: 'scroll' }}>
        <table className="table">
          <tbody>
            { logEntries }
          </tbody>
        </table>
        <div ref={(el) => { this.bottom = el; }} />
      </div>
    );
  }
}

Log.propTypes = {
  Log: PropTypes.array.isRequired,
  Players: PropTypes.array.isRequired,
};

export default Log;



